<template>
    <div>
        <h3 style="margin-left: 100px"> </h3>

        {{board}}
        <el-form style="width: 800px;" >

        <el-form-item label="" style="margin-right: 20px;" label-width="120px" >
        <el-input style="display: inline-block; width: 200px;  " v-model="board.title"></el-input>
        </el-form-item>

        
        <el-form-item label="" style="margin-right: 20px;" label-width="120px" >
        <el-input style="display: inline-block; width: 200px;  " v-model="board.writer"></el-input>
        </el-form-item>

        <el-form-item label=" " label-width="120px">
        <el-input v-model="board.content" type="textarea" style="min-height: 50px"></el-input>
        </el-form-item>
        <el-form-item label-width="120px">
        <el-button type="primary" @click="submitForm"></el-button>
        </el-form-item>
        </el-form>
        

    </div>
</template>

<script>
    export default {
        created(){
            // this.submitForm()
        },
        methods:{
            async submitForm(){
                //   
                // alert('')

                const url = "/board/insert"
                const headers = {"Content-Type" : "application/json"}
                //body raw json   
                // json    
                const body = this.board;
           
                const response = await this.axios.post(url, body, {headers: headers});
                console.log(response);
                if(response.data.status ===200){
                    alert(' ');
                    this.$router.push({name:"Board"})
                }


            }
        },
        data(){
            return{
                board:{
                    title:'',
                    writer: '',
                    content: '',
                }
                
            }
        }
    }
</script>

