<template>
    <div>
        <h5></h5>
        <el-card shadow="always">
            <h3 style="margin-left: 50px"></h3>
            <el-form  label-width="120px">
                <el-form-item :inline = true label="">
                    <el-input placeholder="" 
                    v-model="member.uid" 
                    style="display: inline-block; width: 200px;
                    margin-right: 20px;"
                    ref="uid">
                    </el-input>

                </el-form-item>
                
                <el-form-item label="">
                    <el-input
                        v-model= "member.upw"
                        placeholder=""
                        type="password"
                        style="display: inline-block; width: 200px;
                        margin-right: 20px;"
                        ref="upw">
                    </el-input>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" @click= "handleLogin"></el-button>
                    <el-button @click= "handleJoin"></el-button>
                    
                    <el-button @click= "handelHome"></el-button>
                </el-form-item>
            </el-form>
        </el-card>

    </div>
</template>

<script>
    import {useStore} from 'vuex';
    export default {
        data(){
            return{
                member:{
                    uid: '',
                    upw: '',
                    token : '', //  
                    
                },
                store: useStore()
            }
        },
        methods:{
            handelHome(){
                // this.$router.push({
                //     path:'/home',
                //     // name: 'Home'
                //     })
                // App.vue    
                // App.vue   
                this.store.commit('setMenu', 'home')
            },

            async handleLogin(){
                if(this.member.uid === ''){
                    alert(' ')
                    this.$refs.uid.focus();
                    return false;
                }
                if(this.member.upw ===''){
                    alert(' ')
                    this.$refs.upw.focus();
                    return false;
                }
                //       
                // this.token = '12412k3m123123n123j324jk124n5j111mmmkdf';
                // sessionStorage.setItem("TOKEN", this.token)
                // alert(' .');
                //  ( changeLogged)
                // ( changeLogged  home)
                // this.$emit('changeLogged', 'home')
                // this.$router.push({name:'Home'})
                // this.store.commit('setMenu', 'home')


                const url = '/member/select';
                const headers = {"Content-Type" : "application/json"};
                const body = {
                    uid : this.member.uid,
                    upw : this.member.upw,
                }
                const response = await this.axios.post(
                    url, body, {headers:headers}
                )
                if(response.data.status === 200){
                    alert(' ');
                    //1.   
                    sessionStorage.setItem("TOKEN", response.data.result.token)

                    //2.  .
                    this.store.commit('setMenu', 'home')

                    //3. App.vue   
                }
                else if(response.data.status === 0){
                    alert('  ');
                }
                console.log(response)
            },

            //   
            handleJoin(){
                this.store.commit('setMenu', 'Join')
                // this.$emit('changeLogged', 'home')
                // this.$router.push({name:'Join'})
            }
        }
    }
</script>

