<template>
    <div>
        <el-card shadow="always">
            <h3 style="margin-left: 50px"> </h3>
            <el-form  label-width="120px">
            <el-form-item :inline = true label="">
                <el-input placeholder="" 
                v-model="userpw" 
                style="display: inline-block; width: 200px;
                margin-right: 20px;"
                type="password"
                ref="username">
                </el-input>

            </el-form-item>
            
            <el-form-item label=" ">
                <el-input
                    v-model="userpwchk"
                    placeholder="  "
                    type="password"
                    style="display: inline-block; width: 200px;
                    margin-right: 20px;"
                    ref="useremail">
                </el-input>
            </el-form-item>

            <el-form-item>
                <el-button type="primary" @click= "handleLeave"></el-button>
                
            </el-form-item>
            </el-form>
        </el-card>
    </div>
</template>

<script>
    import {useStore} from 'vuex'
    export default {
        data(){
            return{
                userpw:'',
                userpwchk:'',
                token: sessionStorage.getItem("TOKEN"),
                store : useStore()
            }
        },
        methods:{

            //  
            async handleLeave(){
                if(this.userpw ===''){
                    alert(' ')
                    return false
                }
                if(this.userpwchk ===''){
                    alert(' ')
                    return false
                }
                if(this.userpw !==this.userpwchk){
                    alert(' .')
                    return false
                }
                console.log("MypageComp-3 => handleLeave",1);
                const ret = confirm(' ?')
                if(ret){
                    const url = '/member/mypage?menu=3';
                    const headers = {"Content-Type":"application/json",
                    token : this.token};
                    const body = {userpw : this.userpw};
                    const response = await this.axios.put(
                        url, body, {headers:headers}
                    );
                    console.log(response)
                    // 
                    if(response.data.status ===200){
                        alert(' ');
                        // this.$router.push({name: "Logout"})
                        sessionStorage.removeItem("TOKEN")
                        this.store.commit('setMenu', 'home')
                    }

                    // 
                    else if (response.data.status ===0){
                        alert(' ')
                    }
                    
                }
                
            }
        }
    }
</script>

